.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #000;
  padding: 40px 100px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
  "Lucida Sans Unicode", Geneva, Verdana, sans-serif;}

.left-links, .right-links {
  display: flex;
  gap: 20px;
}

.left-links a, .right-links a {
  text-decoration: none;
  color: #00000067; /* Customize link color */
  padding-bottom: 5px;
}

.speedDial {
  position: fixed;
  right: 3vmax;
  top: 3vmax;
}

.speedDialIcon {
  width: 56px;
  height: 56px;
  border-radius: 100%;
}

.logo a {
  text-decoration: none;
  color: #000000a0; /* Customize text color */
  font-size: 24px; /* Customize font size */
  font-weight: bold; /* Customize font weight */
}

.left-links a.active {
  border-bottom-style: solid;
  border-bottom-width: 2.1px;
  width: fit-content;
  color: black;
  
}

@media only screen and (max-width: 600px) {
 .header{
  display: flex;
  flex-direction: column;
 } 

 .left-links, .right-links{
  margin: 20px 0;
 }
}