.homeHeading {
  text-align: center;
  font-family: Roboto;
  font-size: 1.4vmax;
  border-bottom: 1px solid rgba(21, 21, 21, 0.5);
  width: 20vmax;
  color: rgb(0, 0, 0, 0.7);
}

.container {
  display: flex;
  margin: 0vmax auto;
  width: 98%;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 100%;
  margin-right: 30px;
}

.productCard {
  width: 20vmax;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: rgb(48, 48, 48);
  margin: 10px;
  transition: all 0.5s;
  padding-bottom: 0;
}

.productCard > img {
  width: 20vmax;
  height: 30vmax;
}

.productCard > div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.productCardSpan {
  margin: 0.5vmax;
  font: 300 0.7vmax "Roboto";
}

.productCard > p {
  font-family: "Roboto";
  font-size: 1.2vmax;
  margin: 1vmax 0.5vmax;
  margin-bottom: 0;
}

.productCard > span {
  margin: 0.5vmax;
  color: tomato;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1vmax;
}



/* ProductCard.css */

.productCard {
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

.productImageContainer {
  position: relative;
  overflow: hidden;
}

.productImage {
  width: 100%;
  height: 450px;
  transition: transform 0.3s;
}

.overlay {
  position: absolute;
  bottom: -100%; /* Initially, move it out of view */
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(250, 250, 250, 0.99); /* Grey overlay color with transparency */
  display: flex;
  justify-content: unset;
  align-items: flex-end;
  transition: bottom 0.3s; /* Add a smooth transition */
  color: #000; /* Text color */
  padding: 0px;
}

.productName {
  margin: 15%;
  font-weight: 500;
  font-size: 35px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
  "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}


.productCard:hover .overlay {
  bottom: 0%; /* Slide the overlay in on hover */
}


@media screen and (max-width: 600px) {
  .productCard{
    width: 100%;
    height: 70vmax;
  }
  .productImage {
    width: 100%;
    height: 70vmax;
    transition: transform 0.3s;
  }
  .container{
    margin-right: 4px;
  }
}