footer {
  margin-top: 10vmax;
  padding: 2vmax;
  color: white;
  display: flex;
  align-items: center;
}

.leftFooter {
  width: 20%;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.leftFooter > h4 {
  font-family: "Roboto";
  font-size: 1vmax;
}
.leftFooter > p {
  text-align: center;
  font-size: 1.2vmax;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.leftFooter > img {
  width: 10vmax;
  margin: 1vmax;
  cursor: pointer;
}

.midFooter {
  margin-left: 5%;
}

@media screen and (max-width: 600px) {
  .midFooter{
    margin-left: 150px;
  }
}

.midFooter > h3 {
  font-family: "Roboto";
  color: #00000088;
}
.midFooter > p {
  max-width: 60%;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin: 1vmax auto;
}

.leftFooter {
  width: 20%;

  display: flex;
  flex-direction: row;
  align-items: center;
}
.leftFooter > h4 {
  font-family: "Roboto";
  font-size: 1.4vmax;
  text-decoration: underline;
}
.leftFooter > a {
  text-decoration: none;
  font-size: 1.3vmax;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  transition: all 0.5s;
  margin: 0.5vmax;
  color: black;
}

.leftFooter > a:hover {
  color: #eb4034;
}
